<template>
  <a-table
    bordered
    :loading="loading"
    :columns="tableColumns"
    :data-source="dataSourceWithKey"
    :scroll="{ x: true }"
    :row-selection="{
      selectedRowKeys: selectedRowKeys,
      onChange: onSelectChange,
    }"
  >
    <template slot="tokenId" slot-scope="_, record">
      <div class="flex-center">
        <span :title="record.tokenId">{{
          displayOptimize(record.tokenId)
        }}</span>
        <a-button
          type="primary"
          size="small"
          :id="`btn-${record.key}`"
          :data-clipboard-text="record.tokenId"
          style="margin-left: 10px"
          @click="copyAddr(record.key)"
        >
          
        </a-button>
      </div>
    </template>
    <template slot="workType" slot-scope="_, record">
      <a-tag v-if="record.workType === 1" color="#1890ff"></a-tag>
      <a-tag v-else-if="record.workType === 2" color="#faad14"></a-tag>
    </template>
  </a-table>
</template>

<script>
import ClipboardJs from "clipboard";
import { genUniqueKey } from "../../utils/index";

export default {
  props: {
    dataSource: {
      type: Array,
      required: true,
    },
    loading: {
      type: Boolean,
      default: true,
    },
    value: {
      type: Array,
      required: true,
    },
  },
  data() {
    return {
      selectedRowKeys: [],
    };
  },
  computed: {
    tableColumns() {
      return [
        {
          title: "token id",
          scopedSlots: { customRender: "tokenId" },
        },
        {
          title: "",
          slot: "workType",
          width: 90,
          scopedSlots: { customRender: "workType" },
        },
        {
          title: "",
          width: 90,
          dataIndex: "career",
        },
        {
          title: "",
          width: 90,
          dataIndex: "strength",
        },
        {
          title: "",
          width: 90,
          dataIndex: "agility",
        },
        {
          title: "",
          width: 90,
          dataIndex: "physique",
        },
        {
          title: "",
          width: 90,
          dataIndex: "volition",
        },
        {
          title: "",
          width: 90,
          dataIndex: "brains",
        },
        {
          title: "",
          width: 90,
          dataIndex: "charm",
        },
        {
          title: "",
          width: 100,
          dataIndex: "gold",
        },
      ];
    },
    dataSourceWithKey() {
      return this.dataSource.map((d) => ({ ...d, key: genUniqueKey() }));
    },
  },
  methods: {
    onSelectChange(selectedRowKeys) {
      this.selectedRowKeys = selectedRowKeys;
      console.log(selectedRowKeys);
      this.$emit("input", selectedRowKeys);
    },
    displayOptimize(careerAddr) {
      const res = /(\w{4}).+(\w{4})/.exec(careerAddr);
      return `${res[1]}....${res[2]}`;
    },
    copyAddr(rowKey) {
      const clipboard = new ClipboardJs(`#btn-${rowKey}`);
      clipboard.on("success", () => {
        this.$message.success("");
        clipboard.destroy();
      });
    },
  },
};
</script>

<style scoped>
.flex-center {
  display: flex;
  align-items: center;
}
</style>
